	connect.send("VKWebAppCallAPIMethod", {"method": "users.get", "params": {"user_ids": report.user_id,
	"fields": "photo_100", "v":"5.95", "access_token":this.state.token}, "request_id": 'vopros120'})
	fetch(`https://ivandanilov.pythonanywhere.com/helpers/${report.helper}/?${window.location.search.replace('?', '')}`)
	.then((res) => res.json())
