	getHelpers() {
		fetch(`https://ivandanilov.pythonanywhere.com/helpers/${this.state.fetchedUser.id}/?${window.location.search.replace('?', '')}`)
		.then(response => response.json())
		.then(response => {
			try { var user_id = response.items[0].user_id; }
			catch(e) { return false; }
			if(user_id === "undefiend") {
				return false
			}
			else {
				this.setState({helper: response.items[0]})
				return true
			}
		})
	}
	editVopros() {
		if(this.state.helper_id.id !== this.state.helper.id) {
			return
		}
		this.setState({edits: true});
		this.state.otvet = this.state.edit.otext;
		this.state.edit.otext = '';
	}
	getHelp() {
		fetch(`https://ivandanilov.pythonanywhere.com/reports/?${window.location.search.replace('?', '')}`)
		.then(res => res.json())
		.then(res => {
		var chats = []
		for(var i = 0; i < res.items.length; i++) {
			if(res.items[i].user_id === this.state.fetchedUser.id) {var obj = res.items[i];
			chats.push(obj) }
		}
		this.setState({editrep: true})
		this.setState({myrep: chats}) })
	}
	addOtvet() {
		this.state.edit.otext = this.state.otvet;
		this.state.otvet = '';
		this.state.helper_id.id = this.state.helper.id;
		this.state.helper_id.avatar = this.state.helper.avatar;
		fetch(`https://ivandanilov.pythonanywhere.com/ans_report/${this.state.edit.id}/${this.state.edit.otext}?${window.location.search.replace('?', '')}`)
		if(this.state.edits !== true) {
			this.state.edit.otime = ' ';
			/*connect.send("VKWebAppCallAPIMethod", {"method": "notifications.sendMessage", "params": {"user_ids": '538327743, 242521347, 349128636, 448368288, 258201990, 514061013, 213140458, 298739460, 210533040, 334555354, 204788240',
			"message": "  #"+this.state.helper.id+"    "+this.state.edit.id, "fragment":"adm", 'v': '5.95', 'access_token': secret}})
			connect.send("VKWebAppCallAPIMethod", {"method": "notifications.sendMessage", "params": {"user_ids": this.state.edit.user_id,
		"message": "  #"+this.state.helper.id+"     "+this.state.edit.id, "fragment":"adm", 'v': '5.95', 'access_token': secret}})*/ }
		this.setState({edits: false})
	}
