	addadmin(chat_id, user_id, level, param) {
		switch(level) {
			case '0':
				var text = " "
				var url = `https://ivandanilov.pythonanywhere.com/chats/${chat_id}/deladmin/?${window.location.search.replace('?', '')}&user_ids=${user_id}`; 
				break;
			case '1': 
				var text = "";
				var url = `https://ivandanilov.pythonanywhere.com/chats/${chat_id}/addmoder/${window.location.search.replace('?', '')}&user_ids=${user_id}`; 
				break;
			case '2':
				var text = "";
				var url = `https://ivandanilov.pythonanywhere.com/chats/${chat_id}/addadm/${window.location.search.replace('?', '')}&user_ids=${user_id}`;
				break;
			default:
				break;
		}
		fetch(url)
		.then(res => res.json())
		.then(res => {
			if (res.items.find((e) => e.error)) {
				this.setState({ popout: 
					<Alert 
					actions={[{ 
					title: '', 
					autoclose: true, 
					style: 'cancel' 
					}]} 
					onClose={this.closePopout} 
					> 
					 ,    {text}
					</Alert> 
					});
				return
			}
			this.setState({ popout: 
				<Alert 
				actions={[{ 
				title: '', 
				autoclose: true, 
				style: 'cancel' 
				}]} 
				onClose={this.closePopout} 
				> 
				   {text}
				</Alert> 
				});
				param.level = level;
	})
	}
