	setTitle(chat) {
		if (this.state.title === "" || this.state.title === " ") {
			this.setState({ popout: 
				<Alert 
				actions={[{ 
				title: '', 
				autoclose: true, 
				style: 'cancel' 
				}]} 
				onClose={this.closePopout} 
				> 
				    
				</Alert> 
				})
				return
		}
		if (this.state.title === this.state.currentChat.title) {
			this.setState({ popout: 
				<Alert 
				actions={[{ 
				title: '', 
				autoclose: true, 
				style: 'cancel' 
				}]} 
				onClose={this.closePopout} 
				> 
				     
				</Alert> 
				})
				return
		}
		fetch(`https://ivandanilov.pythonanywhere.com/chats/${chat.chat_id}/rename_title/${this.state.title}?${window.location.search.replace('?', '')}`)
		.then(res => res.json())
		.then(res => {
			if (res.items.find((e) => e.error)) {
				this.setState({ popout: 
					<Alert 
					actions={[{ 
					title: '', 
					autoclose: true, 
					style: 'cancel' 
					}]} 
					onClose={this.closePopout} 
					> 
					 ,    
					</Alert> 
					});
				return
			}
		this.setState({ popout: 
			<Alert 
			actions={[{ 
			title: '', 
			autoclose: true, 
			style: 'cancel' 
			}]} 
			onClose={this.closePopout} 
			> 
			    <b>{this.state.title}</b>
			</Alert> 
			});
			this.state.currentChat.title=this.state.title
			this.setState({activePanel: 'currentChat', activeStory: 'manage', title: ''})
	})
}
